.App{
  width:100%;
  display:flex;
  flex-direction:column;
}
.App-header{
  background-color: #262B38;
  display:flex;
  align-items:center;
}

.App-navbar{
  flex:0.5
}
.App-header>.App-logo{
  color:#fff;
  flex:0.5;
}
.App-logo>img{
  margin-left: 50px;
  height:9vh;
}
.App-navbar >ul{
  display:flex;
  justify-content:space-around;
}
.App-navbar>ul>li{
  list-style:none;
}


@media (max-width: 420px) {
  .App-logo>img{
    margin-left: 0px;
    
  }
}